import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class Start extends Component {
    render() {
        return (
            <div style={{height:300}}>
                <h3 style={{marginLeft:30,marginTop:20}}><Link to='/' style={{textDecoration:"none"}}></Link>/Node.js</h3>
                <h1 style={{marginLeft:30,marginTop:20}}>Node.js</h1>
                <p style={{marginLeft:30,marginTop:20}}> Node.js  npm</p>
                <p style={{marginLeft:30,marginTop:20}}>http://fengmk2.com/blog/2014/03/node-env-and-faster-npm.html</p>
                <p style={{marginLeft:30,marginTop:20}}>Node.js </p>
                <p style={{marginLeft:30,marginTop:20}}>https://github.com/alsotang/node-lessons</p>
            </div>
        )
    }
}
