import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class Api extends Component {
    render() {
        return (
            <div style={{height:200}}>
                <h3 style={{marginLeft:30,marginTop:20}}><Link to='/' style={{textDecoration:"none"}}></Link>/API</h3>
                <h1 style={{marginLeft:30,marginTop:20}}></h1>
                <h4 style={{marginLeft:30,marginTop:20}}>get /topics </h4>
                <h4 style={{marginLeft:30,marginTop:20}}>get /topic/:id </h4>
            </div>
        )
    }
}
